.cross-icon {
 
    display: block;
  }
  
  .line {
    opacity: 0;
    transform: scale(0);
    transform-origin: center;
    animation: draw 0.5s forwards;
  }
  
  .line1 {
    animation-delay: 0.3s; /* Delay for the first line */
  }
  
  .line2 {
    animation-delay: 0.6s; /* Delay for the second line */
  }
  
  @keyframes draw {
    to {
      opacity: 1;
      transform: scale(1);
    }
  }